.programs {
    width: 320px;
    margin: 0 auto;
}
.programs__container {
    padding: 0 20px 20px;
}
.list-program {
    padding: 0;
    margin: 0;
    list-style-type: none;
}
.item-programs {
    position: relative;
    background-color: #F2F2F2;
    margin-bottom: 20px;
    &::after {
        content: "";
        position: absolute;
        width: 240px;
        left: 20px;
        bottom: 57px;
        border: 1px solid #D9D9D9;
    }
}
.item-programs--slim-cat::before {
    content: url("../img/slim-cat.svg");
    position: absolute;
    left: 22px;
    top: 21px;
}
.item-programs--fat-cat::before {
    content: url("../img/fat-cat.svg");
    position: absolute;
    left: 13px;
    top: 24px;
}
.item-programs--bottom {
    margin-bottom: 0 ;
}
.item-programs__title {
    padding-top: 30px;
    margin-left: 91px;
    margin-bottom: 29px;
    margin-top: 0;
}
.title-program {
    font-family: "Oswald", sans-serif;
    font-size: 24px;
    line-height: 37px;
    font-weight: 400;
    text-transform: uppercase;
}
.item-programs__description {
    margin-left: 20px;
    margin-right: 33px;
    margin-top: 0;
    margin-bottom: 42px;
}
.info-program {
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #444444;
}
.item-programs__button {
    margin-left: 20px;
    padding-bottom: 20px;
}
.button-program {
    display: block;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    color: #000000;
}
@media (min-width: 768px) {
  .programs {
    width: 768px;
  }
  .programs__container {
    padding: 0 30px;
  }
  .item-programs__title {
    font-size: 36px;
    line-height: 36px;
    padding: 77px 0 24px 52px;
    margin: 0;
  }
  .item-programs__description {
    font-size: 16px;
    line-height: 24px;
    padding: 0 316px 52px 52px;
    margin: 0;
  }
  .item-programs__button {
    padding: 0 0 57px 52px;
    margin: 0;
  }
  .item-programs {
    &::after {
      content: "";
      width: 0;
      height: 0;
    }
  }
  .item-programs--slim-cat::before {
    content: url("../img/cat-slim-tablet.svg");
    position: absolute;
    left: 446px;
    top: 80px;
  }
  .item-programs--fat-cat::before {
    content: url("../img/cat-fat-tablet.svg");
    position: absolute;
    top: 91px;
    left: 412px;
  }
}
@media (min-width: 1440px) {
  .programs {
    width: 1440px;
  }
  .programs__container {
    padding: 0 110px;
  }
  .item-programs__title {
    font-size: 36px;
    line-height: 36px;
    padding: 77px 0 73px 214px;
    margin: 0;
  }
  .item-programs__description {
    font-size: 16px;
    line-height: 24px;
    padding: 0 51px 27px 52px;
    margin: 0;
  }
  .item-programs__button {
    padding: 0 0 59px 52px;
    margin: 0;
  }
  .list-program {
    display: grid;
    column-gap: 80px;
    grid-template-columns: 570px 570px;
  }
  .item-programs {
    margin: 0;
    &::after {
      content: "";
      width: 0;
      height: 0;
    }
  }
  .item-programs--slim-cat::before {
    content: url("../img/cat-slim-desktop.svg");
    position: absolute;
    top: 41px;
    left: 52px;
  }
  .item-programs--fat-cat::before {
    content: url("../img/cat-fat-desktop.svg");
    position: absolute;
    top: 47px;
    left: 33px;
  }
}

